$icons: (0:0);
$icons: map-merge($icons,('calendar': (X: -51px, Y:0px, W: 26px, H: 26px, TW: 101px, TH: 67px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('call': (X: 0px, Y:-51px, W: 16px, H: 16px, TW: 101px, TH: 67px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('phone': (X: 0px, Y:0px, W: 41px, H: 41px, TW: 101px, TH: 67px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('time': (X: -26px, Y:-51px, W: 15px, H: 15px, TW: 101px, TH: 67px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('user': (X: -87px, Y:0px, W: 14px, H: 16px, TW: 101px, TH: 67px, IMG: '../img/sprite.png')));


// Gets an attribute from the sass map
@function icon-attr($icon, $attr) {
  $icon: map-get($icons, $icon);
  @return map-get($icon, $attr); }

@mixin sprite($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  width: icon-attr($iconName, W);
  height: icon-attr($iconName, H);
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-position($iconName) {
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-retina($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  $width: icon-attr($iconName, W);
  $height: icon-attr($iconName, H);
  width: $width/2;
  height: $height/2;
  $x: icon-attr($iconName, X);
  $y: icon-attr($iconName, Y);
  background-position: $x/2 $y/2;
  $tw: icon-attr($iconName, TW);
  $th: icon-attr($iconName, TH);
  background-size: $tw/2 $th/2; }


@mixin s($i) {
  @include sprite($i); }
@mixin sp($i) {
  @include sprite-position($i); }
@mixin sr($i) {
  @include sprite-retina($i); }


.icon-calendar {
  width: 26px;
  height: 26px;
  background-image: url('../img/sprite.png');
  background-position: -51px 0px; }

.icon-call {
  width: 16px;
  height: 16px;
  background-image: url('../img/sprite.png');
  background-position: 0px -51px; }

.icon-phone {
  width: 41px;
  height: 41px;
  background-image: url('../img/sprite.png');
  background-position: 0px 0px; }

.icon-time {
  width: 15px;
  height: 15px;
  background-image: url('../img/sprite.png');
  background-position: -26px -51px; }

.icon-user {
  width: 14px;
  height: 16px;
  background-image: url('../img/sprite.png');
  background-position: -87px 0px; }
