.why-us {
	background-color: #fff;
	&_default {
		padding: 80px 0 30px;
		@include md {
			padding: 40px 0 20px; } }
	&__container {
		max-width: 1220px;
		padding: 75px 15px 0;
		margin: 0 auto;
		@include md {
			padding: 50px 15px 0; } }
	&__row {
		font-size: 0;
		margin: 0 -15px; }
	&__item {
		display: inline-block;
		vertical-align: top;
		width: 33.33333%;
		padding: 0 15px;
		margin-bottom: 35px;
		@include sm {
			margin-bottom: 20px;
			padding: 0 10px; }
		@include xs {
			width: 50%; }
		@include r(424) {
			width: 100%; }
		&-pic {
			max-width: 230px;
			margin: 0 auto;
			@include sm {
				max-width: 180px; } }
		&-img {
			width: 100%;
			height: 100%; }
		&-text {
			max-width: 292px;
			margin: 0 auto;
			text-align: center;
			@include f('cb', 20);
			line-height: 1.2;
			@include sm {
				font-size: 18px; }
			small {
				font-size: 16px;
				@include sm {
 } } } } }					//font-size: 1px
